.checkmarkContainer {
  width: 25px;
  height: 25px;
  cursor: pointer;
  user-select: none;
  margin-bottom: 12px;
  padding-left: 15px;
  font-size: 22px;
  display: block;
  position: relative;
}

.checkmarkContainer input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  position: absolute;
}

.checkMark {
  height: 25px;
  width: 25px;
  background-color: #fff;
  border-width: 3px;
  border-color: #cdcdcd;
  border-radius: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.checkmarkContainer:hover input ~ .checkMark {
  background-color: #ccc;
}

.checkmarkContainer input:checked ~ .checkMark {
  background-color: #49d861;
}

.checkMark:after {
  content: "";
  display: none;
  position: absolute;
}

.checkmarkContainer input:checked ~ .checkMark:after {
  display: block;
}

.checkmarkContainer .checkMark:after {
  width: 5px;
  height: 10px;
  border: 3px solid #fff;
  border-width: 0 3px 3px 0;
  top: 4px;
  left: 7px;
  transform: rotate(45deg);
}

