    justify-content: space-between;
    padding-top: 2%;
}

.next-Btn {
    height: 100%;
    padding: 10px 20px;
    background-color: #868032;
    color: white;
    font-family: Tropiline;
    font-weight: 600;
    transition: all 0.8s cubic-bezier(0.70, -0.00, 0.57, 0.97) 0.5s;
    opacity: 1;
}

@media (min-width:1280px) {
    .evaluation-nav-pagination {
        flex-basis: 20%;
    }
}

.word-option {
    padding: 10px;
    border: 4px solid var(--surf);
    border-radius: 10px;
    margin-bottom: 10px;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.69, 0.00, 0.45, 0.97);
}

.key-results {
    padding: 10px 20px;
    width: 100%;
    margin-top: 15px;
    font-size: 5.5vw;
    color: #ffffff;
    border-bottom: 1px solid #ffffff;
    font-style: italic;
    word-break: break-word;
}

@media (min-width:768px) {
    .key-results {
        font-size: 3vw;
    }
}

@media (min-width:1280px) {
    .key-results {
        font-size: 2.3rem;
    }
}

@keyframes errorAnimate{
    0% {
        transform: translateX(0px);
    }

    5% {
        transform: translateX(30px);
    }

    15% {
        transform: translateX(-30px);
    }

    30% {
        transform: translateX(20px);
    }

    50% {
        transform: translateX(-20px);
    }

    70% {
        transform: translateX(0px);
    }

    100% {
        transform: translateX(0px);
    }
}

@-moz-keyframes errorAnimate{
    0% {
        transform: translateX(0px);
    }

    100% {
        transform: translateX(100px);
    }
}

@-webkit-keyframes errorAnimate{
    0% {
        transform: translateX(0px);
    }

    100% {
        transform: translateX(100px);
    }
}

.shake {
    background-color: #D37428;
    border: 4px solid #D37428;
    color: white;
    animation: errorAnimate 1s ease-out;
    -webkit-animation: errorAnimate 1s ease-out;
    -moz-animation: errorAnimate 1s ease-out;
}

.evaluationItemComplete {
